import React, { createContext, useReducer, useContext } from "react";

const UserContext = createContext();

const initialState = {
  isOpen: false,
  currentUser: null,
  errorMessage: "",
  successMessage: "", // Add successMessage state
};

// Actions
const SET_CURRENT_USER = "SET_CURRENT_USER";
const CLEAR_CURRENT_USER = "CLEAR_CURRENT_USER";
const SET_ERROR_MESSAGE = "SET_ERROR_MESSAGE";
const CLEAR_ERROR_MESSAGE = "CLEAR_ERROR_MESSAGE";
const SET_SUCCESS_MESSAGE = "SET_SUCCESS_MESSAGE"; // Add action type for setting success message
const CLEAR_SUCCESS_MESSAGE = "CLEAR_SUCCESS_MESSAGE"; // Add action type for clearing success message

const userReducer = (state, action) => {
  switch (action.type) {
    case SET_CURRENT_USER:
      return { ...state, currentUser: action.payload };
    case CLEAR_CURRENT_USER:
      return { ...state, currentUser: null };
    case "TOGGLE_SIDEBAR":
      return { ...state, isOpen: !state.isOpen };
    case SET_ERROR_MESSAGE:
      return { ...state, errorMessage: action.payload };
    case CLEAR_ERROR_MESSAGE:
      return { ...state, errorMessage: "" };
    case SET_SUCCESS_MESSAGE: // Set success message
      return { ...state, successMessage: action.payload };
    case CLEAR_SUCCESS_MESSAGE: // Clear success message
      return { ...state, successMessage: "" };
    case "SET_SCROLL_NAV":
      return { ...state, scrollNav: action.payload };
    case "SET_IS_MOBILE":
      return { ...state, isMobile: action.payload };
    case "CLEAR_COMMENT_FIELDS": // Clear comment and rating fields
      return { ...state, rating: 0, comment: "" };
    default:
      return state;
  }
};

export const UserProvider = ({ children }) => {
  const [state, dispatch] = useReducer(userReducer, initialState);

  const handleSignIn = () => {
    window.location.href = `${process.env.REACT_APP_BACKEND_URL}/auth/google`;
  };

  const handleSignOut = () => {
    dispatch({ type: CLEAR_CURRENT_USER });
  };

  // Function to set error message
  const setErrorMessage = (errorMessage) => {
    dispatch({ type: SET_ERROR_MESSAGE, payload: errorMessage });
  };

  // Function to clear error message
  const clearErrorMessage = () => {
    dispatch({ type: CLEAR_ERROR_MESSAGE });
  };

  // Function to set success message
  const setSuccessMessage = (successMessage) => {
    dispatch({ type: SET_SUCCESS_MESSAGE, payload: successMessage });
  };

  // Function to clear success message
  const clearSuccessMessage = () => {
    dispatch({ type: CLEAR_SUCCESS_MESSAGE });
  };

  // Pass these functions and successMessage through the context
  const contextValue = {
    state,
    dispatch,
    handleSignIn,
    handleSignOut,
    setErrorMessage,
    clearErrorMessage,
    setSuccessMessage,
    clearSuccessMessage,
  };

  return (
    <UserContext.Provider value={contextValue}>
      {children}
    </UserContext.Provider>
  );
};

export const useUserContext = () => useContext(UserContext);